*
{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

footer .footerTop h3 {
  font-weight: normal;
}

footer .footerTop input {
  margin-top: 15px;
  padding: 14px 20px;
  border-radius: 2px;
  margin-right: 10px;
}
footer ul li a {
  color: #fff;
  text-decoration: none;
  padding-bottom: 10px;
  display: inline-block;
}
.Footerlogo
{
font-size: 25px;
}

.footerSocial
{
  color: #fff;
  font-size: 25px;
  margin-right: 20px;
}

@media (max-width: 991px){
  footer .footerTop input {
  width: 100%;
  margin-bottom: 10px;
  }
}
