.menu-icon{
    display: none;
}
.navbar {
    background: #1c2237;
    padding: 10px 80px;
}

.navbar .navbar-container {display: flex;align-items: center;justify-content: space-between;}

.navbar .navbar-container > a {
    color: #fff;
    font-size: 25px;
    text-decoration: none;
    font-weight: bold;
}

.navbar .navbar-container .nav-menu ul {width: auto;}

.navbar .navbar-container .nav-menu ul a {
    color: #fff;
    text-decoration: none;
    font-size: 17px;
    margin-right: 20px;
}

.navbar .navbar-container .nav-menu ul a:last-child {
    margin-right: 0;
}

@media (max-width: 994px){
    .navbar {
            padding: 10px 20px;
    }
    .navbar .navbar-container {
        display: inline-block;
        position: relative;
        width: 100%;
    }
    .menu-icon {
        display: block;
        position: absolute;
        top: 5px;
        right: 0;
    }
    .menu-icon svg {
        color: #fff;
            font-size: 25px;
    }
    .navbar .navbar-container .nav-menu ul a {
        display: inline-block;
        width: 100%;
    }
    .nav-menu {
        position: absolute;
        background: #1c2237;
        top: 40px;
        left: -150%;
        width: calc(100% + 40px);
        height: 90vh;
        transition: all 0.3s ease-out;
    }
    .nav-menu.active {
        left: -20px;
    }
    .nav-menu ul a {
        display: inline-block;
        text-align: center;
        padding: 10px;
        font-size: 22px !important;
        line-height: 20px;
        }
    .navbar .navbar-container .nav-menu ul a {
        margin-right: auto;
        border-bottom: 1px solid #ffffff61;
        line-height: 50px;
    }
    .navbar .navbar-container .nav-menu ul a:last-child a {
        border-bottom: 0;
    }
    .navbar .navbar-container .nav-menu ul a {
    font-weight: 400;
}
   


}