.home__hero-section {
    color: #fff;
    padding: 160px 80px;
}

.home__hero-row {
    align-items: center;
}

.row {
    display: flex;
    margin-right: -15px;
    margin-bottom: -15px;
    margin-left: 15px;
    flex-wrap: wrap;
    align-items: stretch;
}

.col {
    margin-bottom: 15px;
    padding-right: 15px;
    padding-left: 15px;
    flex: 1;
    max-width: 50%;
    flex-basis: 50%;
}

.home__herotext-wrapper {
    max-width: 540px;
    padding-top: 0;
    padding-bottom: 60px;
}
.top-line {
    color: #f00946;
    font-size: 16px;
    line-height: 16px;
    font-weight: 700;
    letter-spacing: 1.4px;
    text-transform: uppercase;
    margin-bottom: 16px;
}
.heading {
    margin-bottom: 24px;
    font-size: 48px;
    line-height: 1.1;
    font-weight: 600;
    color: #1c2237;
}

.dark {
    color: #1c2237;
}

.darkBg {
    background-color: #1c2237;
}
.darkBg p.home__hero-subtitle,
.darkBg .heading {
    color: #f7f8fa;
}
p.home__hero-subtitle {
    max-width: 440px;
    margin-bottom: 35px;
    font-size: 18px;
    line-height: 24px;
    color: #1c2237;
}
.home__hero-img-wrapper {
    max-width: 555px;
    margin-left: auto;
}

.home__hero-img {
    max-width: 95%;
    margin: 0 0 0 10px;
    padding-right: 0;
}

img {
    border: 0;
    max-width: 100%;
    vertical-align: middle;
    display: inline-block;
}

@media screen and (max-width: 991px){
.container{
    padding-left: 30px;
    padding-right: 30px;
}
.home__hero-section {
    padding: 50px 0px;
}

}

@media screen and (max-width: 768px){
    .home__herotext-wrapper   {
        padding-bottom: 65px;
    }
    .col
    {
            max-width: 100%;
            flex-basis: 100%;
    }
}