:root
{
    --primary: #fff;
}
.btn{
padding: 8px 20px;
border-radius: 4px;
outline: none;
border: none;
cursor: pointer;
white-space: nowrap;
}
.btn--primary{
    background-color: var(--primary);
    color: #242d2d;
    border: 1px solid var(--primary);
}
.btn-outline
{
    background-color: transparent;
    color: #fff;
    border: 1px solid var(--primary);
    padding: 8px 20px;
}

.btn-medium{
    padding: 8px 20px;
    font-size: 18px;
}
.btn-large{
    padding: 12px 26px;
    font-size: 20px;
}
.btn--mobile{
    text-align: center;
    border-radius: 4px;
    width: 80%;
    text-decoration: none;
    font-size: 24px;
    background-color: transparent;
    color: #fff;
    padding: 14px 20px;
    border: 1px solid #fff;
    transition: all 0.3s ease-out;
}
.btn--wide{
padding: 12px 64px;
font-size: 20px;
}
.btn--largr:hover,
.btn--medium:hover,
.btn-moile:hover{
    transition: all 0.3s ease-out;
    background: #fff;
    color: #242d2d;
}
.btn--wide:hover{
    color: #fff;
    background-color: #f00946;
    transition: all 0.2s ease-out;

}
.blue{
    color: #fff;
    background-color: #276afb;
    border: none;
}
.blue:hover{
    color: #fff;
    background-color: #f00946;
    border: none;
}
.red{
    color: #fff;
    background-color: #f00946;
    border: none;
}
.primary{
    color: #242d2d;
    background-color: #fff;
    border: 1px solid #242d2d;
}

.primary:hover{
    color: #fff;
    background-color: #242d2d;
    border: 1px solid var(--primary);
}
.green{
    color: #25ce4a;
    background-color: #fff;
    border: none;
}
.green:hover{

    background-color: #242424;

}